.homeSlide {
  margin-top: 15px;
}
.homeSlide h1 {
  font-size: 35px;
  line-height: 55px;
  margin: 20px 0 20px 0;
  padding-left: 20px;
  padding-top: 20px;
}

.homeSlide p {
  margin: 20px 0;
  padding-left: 20px;
}
.homeSlide ul {
  position: absolute;
  bottom: 20px;
}
.homeSlide ul li {
  border: 2px solid #b8b7b7;
  border-radius: 50%;
  padding: 10px;
}

.homeSlide ul li button::before {
  font-size: 15px !important;
  color: #0f3460;
  position: absolute;
  top: 2px;
  left: 0;
  margin: auto;
}
